import getQuery from "../graphql";
import { createApolloFetch } from "apollo-fetch";
import {
    getFilterObject,
    hasFilter,
    getElements,
    isObjectEmpty,
    getFieldName,
    unfilterFields,
    replaceAll,
    getReplaceMap
} from "../utilityFunctions";
import _ from "lodash";
const queryString = require("query-string");
const uuidv4 = require("uuid/v4");

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(
        /[?&]+([^=&]+)=([^&]*)/gi,
        function(m, key, value) {
            vars[key] = value;
        }
    );
    return vars;
}

// Gets the meta-data about a resource (a GHQL element)
export function getId(el) {
    // *Related to `-search` type GHQL element*

    if (el.hasAttribute("data-g-search")) {
        return {
            attributes: null,
            id: el.getAttribute("data-g-id"),
            name: el.getAttribute("data-g-search")
        };
    }

    // *Related to `-resource` type GHQL element*

    // Split the element's `-resource` attribute by `?`
    const parts = el.getAttribute("data-g-resource").split("?");
    // Grab the string before the '?' and store it in variable `name`
    const name = `${parts[0]}`;

    // The string after the `?` contains a query-string that has the id(s)
    // of the resource -- some may need to be replaced with url vars.
    // Save resulting key/value object in variable `attributes`
    const urlVars = getUrlVars();

    if (!_.isEmpty(urlVars)) {
        parts[1] = replaceAll(parts[1], getReplaceMap(urlVars)).trim();
    }

    const attributes = queryString.parse(parts[1]);

    // Break attributes apart into key/value pairs and attach each pair
    // to form second part of a resource id (eg. id123variationId123)
    let attributeString = "";
    Object.entries(attributes).forEach(entry => {
        attributeString += `${entry[0]}${entry[1]}`;
    });

    // We return everything we've scraped together in an object
    return {
        attributes,
        id: `${name}_${attributeString}`,
        name
    };
}

// Given a GHQL element, determins what type it is
// Could either be a `resource` or `search`
export function getResourceType(el) {
    // Assume that prefix-page is a mandatory data-attribute for resourceLists
    if (el.hasAttribute("data-g-page") && el.getAttribute("data-g-page")) {
        return "search";
    }
    return "resource";
}

// Grab the `{ghql:*}` values which will only exist
// in elements that are arrays or objects
export function getChildren(innerHTML) {
    const children = [];
    innerHTML.replace(/\{ghql:([^}]*)\}|\[[^\]]*\]/gi, (match, p1) => {
        children.push(p1);
    });

    return children;
}

export function getResourceFields(el) {
    // Given a GHQL element, return all the `-value` elements Nodes
    const els = getElements(
        `[data-g-${getResourceType(el)}="${el.getAttribute(
            `data-g-${getResourceType(el)}`
        )}"] [data-g-value]`
    );

    // Create a new array called `fields` which will store the elements data
    const fields = Array.from(els).map(field => {
        if (field.innerHTML) {
            let innerHTML = field.innerHTML;
            // Due to the nature of React's controlled components,
            // we opt to nuke innerHTML here, not later in React-land
            // when it would raise rendering errors
            field.setAttribute("data-g-source", innerHTML);
            field.innerHTML = "";
            if (!_.isEmpty(getChildren(innerHTML))) {
                return {
                    name: field.getAttribute("data-g-value"),
                    children: getChildren(innerHTML)
                };
            }
        }
        return {
            name: field.getAttribute("data-g-value"),
            children: null
        };
    });
    return _.uniqBy(fields, "name");
}

// Takes an array of field objects and returns an object of filters
// sorted by paths
export function getFilters(fields) {
    const filters = {};

    for (let i = 0; i < fields.length; i++) {
        if (fields[i].name.includes("[")) {
            const fieldParts = fields[i].name.split(".");
            let filterPath = "";
            fieldParts.forEach(part => {
                if (part.includes("[")) {
                    const filterObject = getFilterObject(part);
                    filterPath += getFieldName(part) + ".";
                    const path = filterPath.slice(0, -1);
                    filters[path] = {
                        path: path,
                        filter: filterObject.filter,
                        equals: filterObject.filterAmount,
                        comparisonOperator: filterObject.comparisonOperator
                    };
                }
            });
        }
    }
    return filters;
}

export function getData(
    resources,
    fieldIdsByResourceId,
    fieldsById,
    filters,
    url
) {
    const apolloFetch = createApolloFetch({ uri: url });

    const query = getQuery(
        resources,
        fieldIdsByResourceId,
        fieldsById,
        filters
    );

    apolloFetch.use(({ request, options }, next) => {
        if (!options.headers) {
            options.headers = {}; // Create the headers object if needed.
        }
        options.headers["X-Application-Key"] =
            "live_1963e55eb42f26ea691feb9c1cd8d0065c80fdef";
        options.headers["Content-Type"] = "application/json";

        next();
    });

    return apolloFetch({ query: query })
        .then(result => {
            return {
                result: result,
                query: query
            };
            //GraphQL errors and extensions are optional
        })
        .catch(error => {
            //respond to a network error
        });
}

export function getMiddleware(resource) {
    const middleware = {};
    const funcs = getElements(
        `[data-g-${getResourceType(resource)}="${resource.getAttribute(
            `data-g-${getResourceType(resource)}`
        )}"] [data-g-middleware]`
    );
    if (funcs) {
        Array.from(funcs).forEach(field => {
            middleware[
                getFieldName(field.getAttribute("data-g-value"))
            ] = field.getAttribute("data-g-middleware");
        });
    }
    return middleware;
}

export function getResources() {
    const resources = {};
    const views = {};
    const filtersById = {};
    const fieldsById = [];
    const middlewareById = {};

    const els = getElements("[data-g-resource], [data-g-search]");
    if (els && !_.isEmpty(els)) {
        els.forEach(element => {
            const { id, name, attributes } = getId(element);
            const fields = getResourceFields(element);

            if (fields) {
                fields.forEach(field => {
                    const object = {};
                    object.id = uuidv4();
                    object.name = getFieldName(field.name);
                    object.children = field.children;
                    object.parent = id;
                    fieldsById.push(object);
                });
            }

            const filters = getFilters(fields);
            if (!isObjectEmpty(filters)) {
                filtersById[id] = filters;
            }

            const middleware = getMiddleware(element);
            if (!isObjectEmpty(middleware)) {
                middlewareById[id] = middleware;
            }

            const uid = uuidv4();
            views[uid] = id;
            element.setAttribute("data-g-uid", uid);

            // Remove the filter names and values from the GHQL element fields
            // We are already saving these in filtersById
            unfilterFields(element);

            resources[id] = {
                id,
                name,
                attributes,
                type: getResourceType(element)
            };
        });
    }
    return { resources, views, filtersById, fieldsById, middlewareById };
}



