import { put, select, call } from "redux-saga/effects";
import {
    filterData,
    applyMiddleware,
    isObjectEmpty,
    applyFilters,
    hydrateResources
} from "../utilityFunctions";
import _ from "lodash";
import {
    getResourceId,
    getResourceFields,
    getData,
    getResources
} from "../getters";
import localforage from "localforage";
import { getFieldIdsByResourceId } from "../selectors";

function setCache(payload) {
    return localforage
        .setItem("ghql.resources", payload)
        .then(function(value) {
            return value;
        })
        .catch(function(err) {
            console.log(err);
        });
}

function getCache() {
    return localforage
        .getItem("ghql.resources")
        .then(function(value) {
            return value;
        })
        .catch(function(err) {
            console.log(err);
        });
}

export function* fetchResourcesAsync() {
    const {
        resources,
        filtersById,
        views,
        fieldsById,
        middlewareById
    } = yield getResources();
    const useCache = yield select(state => state.config.cache);
    if (useCache) {
        const cache = yield call(getCache);
        const updatedResources = yield hydrateResources(resources, cache);
    }
    yield put({ type: "SET_RESOURCES", payload: resources });
    yield put({ type: "SET_FILTERS", payload: filtersById });
    yield put({ type: "SET_MIDDLEWARE", payload: middlewareById });
    yield put({ type: "SET_VIEWS", payload: views });
    yield put({ type: "LOAD_FIELDS", payload: fieldsById });
    yield put({ type: "FETCH_DATA" });
}

export function* fetchDataAsync() {
    const resources = yield select(state => state.resources.byId);
    const filters = yield select(state => state.filters);
    const middleware = yield select(state => state.middleware);
    const fieldIdsByResourceId = yield select(state =>
        getFieldIdsByResourceId(state)
    );
    const fieldsById = yield select(state => state.fields.byId);
    const url = yield select(state => state.config.url);

    if (!isObjectEmpty(resources)) {
        try {
            const { result, query } = yield getData(
                resources,
                fieldIdsByResourceId,
                fieldsById,
                filters,
                url
            );
            yield put({ type: "SET_QUERY", payload: query });
            if (result && result.errors) {
                console.error("graphql errors", result.errors);
            }

            const wereErrors = result.errors;

            if (!wereErrors) {
                // Extract data from the result returned from the POST
                const { data } = result;

                // Do various transformations to the data
                const filteredData = applyFilters(_.cloneDeep(data), filters);
                applyMiddleware(filteredData, middleware);

                // Update the resources reducer with data
                // This will trigger ResourceComponent to update and re-render
                const updatedResources = yield hydrateResources(
                    resources,
                    filteredData
                );
                yield put({ type: "SET_RESOURCES", payload: updatedResources });

                const useCache = yield select(state => state.config.cache);
                if (useCache) {
                    const setResponse = yield call(setCache, filteredData);
                }
            } else {
                yield put({ type: "SET_ERRORS", payload: result.errors });
            }
        } catch (e) {
            console.error(e);
        }
    }
}



