export const addResources = payload => ({
    type: "SET_RESOURCES",
    payload
});

export const fetchResources = () => ({
    type: "FETCH_RESOURCES"
});

export const resourcesFetchSuccessAction = resources => {
    return {
        type: "SUCCESS_FETCH_RESOURCES",
        payload: resources
    };
};

export const resourcesFetchFailAction = err => {
    return {
        type: "FAIL_FETCH_RESOURCES",
        err
    };
};



