import React from "react";
import ReactDOM from "react-dom";
import App from "./components/App";
import store from "./store/store";
import { fetchResources } from "./actions/resources.js";
import { Provider } from "react-redux";
import configureCache, {
    configureUrl,
    configureGlobals
} from "./config/configureCache";

const domNode = document.querySelectorAll("[data-g-config]")[0];

if (domNode) {
    configureCache(domNode);
    configureUrl(domNode);

    configureGlobals();
    store.dispatch(fetchResources());

    ReactDOM.render(
        <Provider store={store}>
            <App />
        </Provider>,
        domNode
    );
}



