import _ from "lodash";
import { getResourceType } from "./getters";

const acorn = require("acorn");
const walk = require("acorn/dist/walk");

export const setValue = (object, newValue, path) => {
    var stack = path.split(".");
    while (stack.length > 1) {
        object = object[stack.shift()];
    }
    object[stack.shift()] = newValue;
};

export const getValue = (pathString, resource) => {
    const path = _.toPath(pathString);
    return path.length
        ? getResourceValueWithPath(resource, ...path)
        : undefined;
};

export function getResourceValueWithPath(resource, prop, ...restOfPath) {
    if (!resource) return resource;
    return restOfPath.length > 0
        ? getResourceValueWithPath(resource[prop], ...restOfPath)
        : resource[prop];
}

export function getType(value) {
    if (typeof value === "string" || typeof value === "number") {
        return "String";
    } else if (value instanceof Object && value.constructor === Object) {
        return "Object";
    } else if (Array.isArray(value)) {
        return "Array";
    }
}

export function getTarget(el) {
    return el.cloneNode(true);
}

export const getFilterObject = value => {
    const name = value.slice(0, value.indexOf("["));
    const filterString = value.slice(value.indexOf("["));

    let comparisonOperator;
    if (filterString.includes("=")) {
        comparisonOperator = "=";
    } else if (filterString.includes("~")) {
        comparisonOperator = "~";
    }

    const filter = filterString.slice(
        1,
        filterString.indexOf(comparisonOperator)
    );
    const filterAmount = filterString.slice(
        filterString.indexOf(comparisonOperator) + 1,
        filterString.indexOf("]")
    );
    return {
        name,
        filter,
        filterAmount,
        comparisonOperator
    };
};

export function getFieldName(value) {
    if (!value.includes("[") && !value.includes("(")) {
        return value;
    }
    if (value.includes("[")) {
        return value.replace(/ *\[[^\]]*]/g, "");
    } else if (value.includes("(")) {
        return getFieldName(value.slice(0, value.indexOf("(")));
    }

    return value;
}

function filterArray(filterObj, array) {
    if (array) {
        let returnObj;

        switch (filterObj.comparisonOperator) {
            case "=":
                returnObj = array.find(
                    item => item[filterObj.filter] === filterObj.equals
                );
                break;
            case "~":
                returnObj = array.find(item =>
                    item[filterObj.filter].includes(filterObj.equals)
                );
            default:
                break;
        }

        if (!returnObj) {
            returnObj = array[0];
        }

        return returnObj;
    }
}

const processFilter = ({ filters, data }) =>
    Object.keys(filters).forEach(path =>
        setValue(data, filterArray(filters[path], getValue(path, data)), path)
    );

export const applyFilters = (data, filters) => {
    Object.keys(data).forEach(id => {
        const allFilters = filters[id];
        const resource = data[id];

        if (!allFilters || !resource) return;

        if (Object.keys(resource)[0] == "results") {
            resource.results.forEach(resource => {
                processFilter({
                    filters: allFilters,
                    data: resource
                });
            });
        } else {
            processFilter({
                filters: allFilters,
                data: resource
            });
        }
    });
    return data;
};

export function hydrateResources(resources, data) {
    if (!data) return resources;
    const entries = Object.entries(resources);
    const newResources = {};

    entries.forEach(entry => {
        const newData = data[entry[0]];
        let newEntry = entry[1];
        newEntry.data = newData;
        newResources[entry[0]] = newEntry;
    });

    return newResources;
}

export const applyMiddleware = (data, middlewares) => {
    Object.keys(data).forEach(id => {
        const middleware = middlewares[id];
        if (middleware) {
            Object.keys(middleware).forEach(name => {
                const funcString = middleware[name];
                // `name` could be a path-string (foo.bar)
                const value = getValue(name, data[id]);
                if (funcString && value) {
                    walk.simple(acorn.parse(funcString), {
                        FunctionExpression(node) {
                            const param = node.params[0].name;
                            const bodyStart = node.body.start;
                            const bodyEnd = node.body.end;
                            const body = funcString
                                .slice(bodyStart + 1, bodyEnd - 1)
                                .trim();
                            const func = new Function(param, body);
                            const transformedValue = func(value);
                            setValue(data[id], transformedValue, name);
                        }
                    });
                }
            });
        }
    });
    return data;
};

// Replace string with multiple searches
// Pass the str and a map of the replacements
// and it would return the middlewareed string.
export function replaceAll(str, mapObj) {
    var re = new RegExp(Object.keys(mapObj).join("|"), "gi");

    return str.replace(re, function(matched) {
        return mapObj[matched];
    });
}

export function isObjectEmpty(obj) {
    return Object.keys(obj).length === 0 && obj.constructor === Object;
}

export function getElements(elmSel) {
    return document.querySelectorAll(elmSel);
}

export function getReplaceMap(replace) {
    const flatten = require("flat");
    const flatReplace = flatten(replace);
    let replaceMap = {};
    Object.keys(flatReplace).forEach(string => {
        replaceMap[`{ghql:${string}}`] = flatReplace[string];
    });
    return replaceMap;
}

// Returns parsed JSON from an element's data-attribute
export function getJSON(el, attributeName) {
    return JSON.parse(el.getAttribute("data-g-config").trim());
}

export function unfilterFields(el) {
    const els = getElements(
        `[data-g-${getResourceType(el)}="${el.getAttribute(
            `data-g-${getResourceType(el)}`
        )}"] [data-g-value]`
    );
    const fields = Array.from(els).map(field => {
        field.setAttribute(
            "data-g-value",
            getFieldName(field.getAttribute("data-g-value"))
        );
    });
}

export function getElement(uid, name) {
    return getElements(`
        [data-g-uid="${uid}"] [data-g-value="${name}"]
    `)[0];
}



