import React from "react";
import ReactDOM from "react-dom";
import { replaceAll, getReplaceMap } from "../utilityFunctions";

const HtmlToReactParser = require("html-to-react").Parser;
const htmlToReactParser = new HtmlToReactParser();

class FieldComponent extends React.Component {
    componentWillReceiveProps(nextProps) {
        if (
            JSON.stringify(this.props.value) !== JSON.stringify(nextProps.value)
        ) {
            this.setState({
                value: nextProps.value,
                type: nextProps.type
            });
        }
    }
    constructor(props) {
        super(props);
        this.state = { ...props };
    }
    renderValue() {
        const { source, value, field } = this.state;
        if (!source) {
            return ReactDOM.createPortal(value, field);
        }
        return ReactDOM.createPortal(
            htmlToReactParser.parse(source.replace(/{value}/g, value).trim()),
            field
        );
    }
    renderObject() {
        const { source, value, field } = this.state;
        if (!source) return null;

        return ReactDOM.createPortal(
            htmlToReactParser.parse(
                replaceAll(source, getReplaceMap(value)).trim()
            ),
            field
        );
    }
    renderLoop() {
        const { source, value, field } = this.state;
        if (!this.state.source) return null;

        const renderStack = [];
        value.forEach((data, index) => {
            renderStack.push(
                replaceAll(source, getReplaceMap(data))
                    .replace("{id}", index + 1)
                    .trim()
            );
        });

        return ReactDOM.createPortal(
            htmlToReactParser.parse(renderStack.join("")),
            field
        );
    }
    render() {
        if (!this.state.value) return null;

        switch (this.state.type) {
            case "String":
                return this.renderValue();
            case "Object":
                return this.renderObject();
            case "Array":
                return this.renderLoop();
        }

        return null;
    }
}

export default FieldComponent;



