import React from "react";
import ResourceComponent from "../components/ResourceComponent";
import ErrorComponent from "../components/ErrorComponent";
import { connect } from "react-redux";
import { isObjectEmpty } from "../utilityFunctions";

const uuidv4 = require("uuid/v4");

class App extends React.Component {
    renderError() {
        return <ErrorComponent />;
    }
    renderViews() {
        const { views, resourcesById } = this.props;
        return Object.keys(views).map(viewId => {
            const resource = resourcesById[views[viewId]];
            if (resource.type === "resource") {
                return (
                    <ResourceComponent
                        key={viewId}
                        uid={viewId}
                        resource={resource}
                    />
                );
            } else {
                if (resource.data) {
                    return resource.data.results.map((data, i) => {
                        return (
                            <ResourceComponent
                                key={uuidv4()}
                                uid={viewId}
                                data={data}
                                dataId={i}
                                isList={true}
                                resource={resource}
                            />
                        );
                    });
                }
            }
        });
    }
    render() {
        return !isObjectEmpty(this.props.errors)
            ? this.renderError()
            : this.renderViews();
    }
}

export default connect(state => ({
    views: state.views,
    errors: state.errors,
    resourcesById: state.resources.byId
}))(App);



