export const getQuery = (
    resources,
    fieldIdsByResourceId,
    fieldsById,
    filters
) => {
    let qs = `{ `;
    Object.keys(resources).forEach(id => {
        const resource = resources[id];
        const fields = fieldIdsByResourceId[id];
        const fieldNames = [];

        qs +=
            resource.type === "resource"
                ? addResource(resource)
                : addListResource(resource);
        fields &&
            fields.map(field => {
                if (!fieldNames.includes(fieldsById[field].name)) {
                    qs += `${addField(fieldsById[field], filters[id])}`;
                }
                fieldNames.push(fieldsById[field].name);
            });
        qs += resource.type === "resource" ? "}, " : "}}}, ";
    });
    qs += `} `;
    return qs;
};

export default getQuery;

export function addResource(resource) {
    let attributes = "";
    const entries = Object.entries(resource.attributes);
    for (let i = 0; i < entries.length; i++) {
        const entry = entries[i];
        attributes += `${entry[0]}: ${entry[1]} `;
        if (i != entries.length - 1) {
            attributes += ", ";
        }
    }
    return `${resource.id}: ${resource.name}(${attributes.slice(0, -1)}) { `;
}

export function addField(field, filters) {
    const fieldPath = field.name.split(".");
    let segment = "";
    let path = "";
    fieldPath.forEach((value, index, array) => {
        path += value + ".";
        segment += getSegment(
            value,
            array.length,
            field,
            path.slice(0, -1),
            filters,
            index === array.length - 1
        );
    });
    return segment + addClosingBrackets(fieldPath.length);
}

function explodePathsIntoQuery(children) {
    let string = "";
    children.forEach(child => {
        const paths = child.split(".");
        paths.forEach((path, i) => {
            string += ` ${path} `;
            if (paths.length - 1 > i) {
                string += ` { `;
            }
        });
        string += addClosingBrackets(paths.length);
    });
    return string;
}

export function getSegment(value, arrayLength, field, path, filters, isLast) {
    if (filters && Object.keys(filters).includes(path)) {
        const filterObject = filters[path];
        return `${value} { ${filterObject.filter} `;
    }
    if (field.children && isLast) {
        const fieldChildren = explodePathsIntoQuery(field.children);
        return `${value} { ${fieldChildren} }`;
    }
    if (!isLast) {
        return ` ${value} { `;
    }
    return `${value} `;
}

function addClosingBrackets(x) {
    return Array(x).join(` }`);
}

function makeSearchQuery(must, mustnot) {
    let query = "query: {";
    const action = must ? "must" : "mustNot";
    const match = must ? must : mustnot;

    if (mustnot || must) {
        query += `${action}: [
            {
                match: {
                    operator: ${match.operator},
                    attr: "${match.attr}"
                    value:"${match.value}"
                }
            },
        ]`;
    }
    query += "}";
    return query;
}

function addListResource(resource) {
    // const { page, pagesize, mustnot, must } = resource.queryVars
    // const searchq = makeSearchQuery(must, mustnot)
    return `
    ${resource.id}:
        search(
            resource: ${resource.name.toUpperCase()}
            page: 1,
            pageSize: 2,
            query: {}
          ){
            results {
              ... on DeparturesType {
        `;
}



