import { combineReducers } from "redux";
const byId = (state = {}, action) => {
    switch (action.type) {
        case "SET_RESOURCES":
        case "SUCCESS_FETCH_RESOURCES":
            return action.payload;
        case "FAIL_FETCH_RESOURCES":
            return state;

        default:
            return state;
    }
};

const resourcesReducer = combineReducers({
    byId
});

export default resourcesReducer;



