import React from "react";
import FieldComponent from "./FieldComponent";
import { connect } from "react-redux";
import localforage from "localforage";
import { getFieldIdsByResourceId } from "../selectors";
import {
    getValue,
    getElement,
    getElements,
    getType,
    getTarget
} from "../utilityFunctions";
import _ from "lodash";

class ResourceComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = { ...props };
    }
    componentWillReceiveProps(nextProps) {
        if (this.props !== nextProps) {
            this.setState({ ...nextProps });
        }
    }

    render() {
        const {
            resource,
            uid,
            fieldIdsByResourceId,
            fieldsById,
            data,
            dataId,
            isList = false
        } = this.state;

        if (!_.isEmpty(fieldIdsByResourceId)) {
            const fieldNamesProcessed = [];
            return fieldIdsByResourceId[resource.id].map(fieldId => {
                const field = fieldsById[fieldId];
                if (!fieldNamesProcessed.includes(field.name)) {
                    let theField;
                    let value;

                    if (!isList) {
                        theField = getElement(uid, field.name);
                        value = getValue(field.name, resource.data);
                    } else {
                        // List type resource
                        // (A series of "virtual" resources)
                        const matches = Array.from(
                            getElements(`
                            [data-g-uid="${uid}"] [data-g-value="${field.name}"]
                        `)
                        );

                        // The field could either pre-exist (if cache = true)
                        // or needs to be created + appended to the DOM
                        theField = matches[dataId];
                        if (!theField) {
                            const resourceEl = getElements(
                                `[data-g-uid="${uid}"]`
                            )[0];
                            let fieldCopy = matches[0].cloneNode(true);
                            fieldCopy.innerHTML = "";
                            resourceEl.appendChild(fieldCopy);
                            theField = fieldCopy;
                        }

                        value = getValue(field.name, data);
                    }

                    return (
                        <FieldComponent
                            key={field.id}
                            value={value}
                            type={getType(value)}
                            field={theField}
                            source={theField.getAttribute("data-g-source")}
                        />
                    );

                    return null;
                }
            });
        }
        return null;
    }
}

export default connect(state => ({
    resources: state.resources,
    fieldsById: state.fields.byId,
    fieldIdsByResourceId: getFieldIdsByResourceId(state)
}))(ResourceComponent);



