import { combineReducers } from "redux";
const byId = (state = {}, action) => {
    switch (action.type) {
        case "LOAD_FIELDS":
            const newState = {};
            action.payload.map(field => {
                newState[field.id] = {
                    id: field.id,
                    name: field.name,
                    parent: field.parent,
                    children: field.children
                };
            });
            return newState;

        default:
            return state;
    }

    return state;
};

const ids = (state = [], action) => {
    switch (action.type) {
        case "LOAD_FIELDS":
            return [...state, ...action.payload.map(item => item.id)];
    }

    return state;
};

const fieldsReducer = combineReducers({
    byId,
    ids
});

export default fieldsReducer;



