import { combineReducers } from "redux";
import resourcesReducer from "./resources";
import fieldsReducer from "./fields";
import configReducer from "./config";

import generateGenericReducer from "./generator";

export default combineReducers({
    resources: resourcesReducer,
    fields: fieldsReducer,
    config: configReducer,
    middleware: generateGenericReducer("SET_MIDDLEWARE"),
    errors: generateGenericReducer("SET_ERRORS"),
    filters: generateGenericReducer("SET_FILTERS"),
    views: generateGenericReducer("SET_VIEWS"),
    query: generateGenericReducer("SET_QUERY")
});



