import { createSelector } from "reselect";

const getFieldsById = state => state.fields.byId;

export const getFieldIdsByResourceId = createSelector(
    [getFieldsById],
    fields => {
        const fieldIdsByResourceId = {};
        Object.keys(fields).map(field => {
            const parent = fields[field].parent;
            if (!fieldIdsByResourceId[parent]) {
                fieldIdsByResourceId[parent] = [];
            }
            fieldIdsByResourceId[parent].push(field);
        });
        return fieldIdsByResourceId;
    }
);



