const configReducer = (
    state = {
        cache: true
    },
    action
) => {
    switch (action.type) {
        case "SET_CACHE":
            return {
                ...state,
                cache: action.payload
            };
        case "SET_URL":
            return {
                ...state,
                url: action.payload
            };
        case "SET_GLOBALS":
            return {
                ...state,
                globals: action.payload
            };
    }

    return state;
};

export default configReducer;



