import React from "react";
import { connect } from "react-redux";
import { isObjectEmpty } from "../utilityFunctions";
import {
    ErrorTitle,
    Container,
    InnerContainer,
    Message,
    Background,
    Query
} from "../styles/styles";

const ErrorMessage = props => {
    return (
        <div>
            <Container>
                <InnerContainer>
                    <Container>
                        <Message>{props.message}</Message>
                    </Container>
                    <Container>
                        <Query>
                            <div>{props.query}</div>
                        </Query>
                    </Container>
                </InnerContainer>
            </Container>
        </div>
    );
};

class ErrorComponent extends React.Component {
    render() {
        const { query, errors } = this.props;
        if (errors && !isObjectEmpty(errors)) {
            return (
                <Background>
                    <ErrorTitle>Query Error!</ErrorTitle>
                    {errors.map((error, i) => {
                        const errPos = error.locations[0].column;
                        const formattedQuery = [
                            query.slice(0, errPos - 1),
                            "-->",
                            query.slice(errPos - 1)
                        ].join("");
                        return (
                            <ErrorMessage
                                key={i}
                                query={formattedQuery}
                                message={error.message}
                            />
                        );
                    })}
                </Background>
            );
        } else {
            return null;
        }
    }
}

export default connect(state => ({
    errors: state.errors,
    query: state.query
}))(ErrorComponent);



