import { fetchResourcesAsync, fetchDataAsync } from "./workers";
import { takeEvery } from "redux-saga/effects";

// Watchers
export function* watchFetchResourcesAsync() {
    yield takeEvery("FETCH_RESOURCES", fetchResourcesAsync);
}

export function* watchFetchDataAsync() {
    yield takeEvery("FETCH_DATA", fetchDataAsync);
}



