import store from "../store/store";
import { getJSON } from "../utilityFunctions";
import { DATA_CONFIG } from "../constants";

// Checks to see if cache should be turned ON or OFF
export default function(domNode) {
    if (domNode) {
        try {
            const data = getJSON(domNode, DATA_CONFIG);
            if (data && data.userCache && data.useCache === false) {
                // Disable the cache
                store.dispatch({
                    type: "SET_CACHE",
                    payload: false
                });
            }
        } catch (e) {
            console.error("Could not read cache config. Error: ", e);
        }
    }
}

export const configureUrl = domNode => {
    if (domNode) {
        try {
            const data = getJSON(domNode, DATA_CONFIG);
            if (data && data.url) {
                store.dispatch({
                    type: "SET_URL",
                    payload: data.url
                });
            }
        } catch (e) {
            console.error("Could not read config. Error: ", e);
        }
    }
};

const getGlobals = new Promise((resolve, reject) => {
    const vars = getUrlVars();
    if (vars) {
        resolve(vars);
    } else {
        reject(Error("Error accessing URL vars"));
    }
});

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(
        /[?&]+([^=&]+)=([^&]*)/gi,
        function(m, key, value) {
            vars[key] = value;
        }
    );
    return vars;
}

export const configureGlobals = () => {
    getGlobals.then(response => {
        store.dispatch({
            type: "SET_GLOBALS",
            payload: response
        });
    });
};



